//HEADER
.header{
    width: 100vw;
    height: 100px;
    display: flex;
    overflow-x: hidden;
    overflow: visible ;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    padding: 0 30px;
    box-sizing: border-box;
    background-color: #fff;
    box-shadow: 0px 4px 20px #eee;
    z-index: 15;
    &__logo{
        width: 180px;
        margin: 0;
        background-image: url(../assets/images/logo-mobile.svg);
        background-repeat: no-repeat;
        background-size: 100%;
        background-position: center;
        overflow: hidden;
        text-indent: 100%;
        cursor: pointer;
    }
    &__burger{
        width: 40px;
        text-indent: -9999px;
        & .burgerIcon, & .burgerIcon:before, & .burgerIcon:after{
            height: 1.2px;
            width: 40px;
            background: #000;
            position: absolute;
            display: block;
            content: '';
            transition: all 0.5s ease-in-out;
            z-index: 15;
            cursor: pointer;
        }
        & .burgerIcon{
            &:before{
                animation: toNormalStateTop .5s forwards ease;
            }
            &:after{
                bottom: -10px;
                animation: toNormalStateBottom .5s ease;
            }
        }
        &--open{
            & .burgerIcon{
                background-color: transparent;
            }
            & .burgerIcon:before{
                top: 0;
                transform: translateY(0px);
                animation: toCrossTop .5s forwards ease;
            }
            & .burgerIcon:after{
                transform: translateY(0px);
                animation: toCrossBottom .5s forwards ease;
            }
        }
    }
    &__content{
        width: 100%;
        height: 100vh;
        position: fixed;
        box-sizing: border-box;
        top: 0;
        left: 100%;
        background-color: #fff;
        transition: .5s ease;
        &--open{
            left: 0;
        }
    }
    &--onScroll{
        height: 100px;
    }
}
.coronaAlert{
    position: absolute;
    top: 120px;
    left: 50%;
    transform: translateX(-50%);
    &__button{
        position: absolute;
        top: -55px;
        right: 45px;
        width: 50px;
        height: 50px;
        overflow: hidden;
        background-color: $c-blue;
        border-radius: 50%;
        text-indent: -999px;
        background-image: url(../assets/images/alert.svg);
        background-repeat: no-repeat;
        background-size: 55%;
        background-position: center;
        transition: ease .4s;
        cursor: pointer;
        &--open{
            background-image: url(../assets/images/close.svg);
        }
    }
    &__content{
        width: calc(100vw - 60px);
        max-width: 470px;
        box-sizing: border-box;
        opacity: 0;
        visibility: hidden;
        background-color: $c-blue;
        padding: 20px;
        transition: .4s ease-out;
        border-radius: 30px;
        &--visible{
            opacity: 1;
            visibility: visible;
        }
        & > :first-child{
            margin-bottom: 20px;
            font-size: $fs-subtitle;
        }
        & > :nth-child(2){
            font-size: $fs-bigText;
        }
    }
}
//to normal state animations
@keyframes toNormalStateTop { 
    0% { transform: translateY(0px) rotate(45deg); }
    50% { transform: translateY(0px) rotate(0); }
    100% { transform: translateY(-10px) rotate(0); }
}
@keyframes toNormalStateBottom {
    0% { transform: translateY(-10px) rotate(-45deg); }
    50% { transform: translateY(-10px) rotate(0); }
    100% { transfrom: translateY(0px) rotate(0); }
}
//to cross animations
@keyframes toCrossTop {
    0% { transform: translateY(-10px) rotate(0); }
    50% { transform: translateY(0px) rotate(0); }
    100% { transform: translateY(0px) rotate(45deg); }
}
@keyframes toCrossBottom {
    0% { transfrom: translateY(0) rotate(0); }
    50% { transform: translateY(-10px) rotate(0); }
    100% { transform: translateY(-10px) rotate(-45deg); }
}
//media queries
@media(min-width: 650px){
    .coronaAlert{
        width: fit-content;
        left: initial;
        top: 150px;
        right: 120px;
        transform: none;
        &__button{
            right: -25px;
        }
        &__content{
            border-radius: 30px 0 30px 30px;
            & > :first-child{
                margin-bottom: 20px;
                font-size: $fs-bigText;
            }
            & > :nth-child(2){
                font-size: $fs-text;
            }
        }
    }
    .header{
        height: 120px;
        padding: 0 60px;
        &__logo{
            width: 200px;
        }
        &__content{
            padding: 0 80px;
            &:after{
                content:'';
                position: absolute;
                bottom: -240px;
                right: -200px;
                width: 520px;
                height: 520px;
                background-color: $c-lightBlue;
                border-radius: 50%;
            }
        }
    }
}
//hover effects
@media(min-width: 950px){
    .coronaAlert{
        &__button{
            &:hover{
                background-image: url(../assets/images/alert-white.svg);
                background-color: $c-darkBlue;
            }
            &--open{
                &:hover{
                    background-image: url(../assets/images/close.svg);
                    background-color: $c-blue;
                }
            }
        }
    }
}
@media(min-width: 1150px){
    .header{
        height: 120px;
        padding: 0 80px;
        &__burger{
            display: none;
        }
        &__content{
            width: auto;
            max-height: 100px;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            position: relative;
            top: 0;
            left: 0;
            padding: 0;
            background-color: transparent;
            &:after{
                display: none;
            }
        }
    }
    .coronaAlert{
        top: 150px;
        right: 60px;
        &__button{
            top: -50px;
            width: 40px;
            height: 40px;
        }
    }
}
@media(min-width: 1300px){
    .header{
        padding: 0 145px;
    }
    .coronaAlert{
        right: 125px;
    }
}