//LISTS
ul{
    list-style: none;
    padding: 0;
}
//header
.headerList{
    width: 70%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    &__el{
        color: #000;
        transition: all 0.2s ease;
        cursor: pointer;
    }
    &--small{
        position: absolute;
        bottom: 30px;
        width: 250px;
        margin: 120px 0 0 0;
        left: 50%;
        transform: translateX(-50%);
        & .headerList__el{
            width: fit-content;
            margin: 0 10px;
            & .socialMedia{
                &__el{
                    opacity: 1;
                }
            }
        }
    }
    &--main{
        flex-direction: column;
        align-items: flex-start;
        margin: 90px auto 0 auto;
        & .headerList__el{
            font-size: $fs-subtitle;
            & > .dropdownLink{
                &:after{
                    content: '';
                    position: absolute;
                    width: 20px;
                    height: 20px;
                    top: 0;
                    right: -30px;
                    background-image: url(../assets/images/arrow.svg);
                    background-repeat: no-repeat;
                    background-position: bottom;
                    transition: all 0.2s ease;
                }
                &--open{
                    &:after{
                        transform: rotate(180deg);
                        transform-origin: center 75%;
                    }
                }
            }
            &:not(:last-child){
                margin-bottom: 30px;
            }
        }
    }
}
.dropdownLink{
    position: relative;
    cursor: pointer;
    transition: all 0.2s ease;
}
.dropdownList{
    display: none;
    opacity: 0;
    visibility: hidden;
    transition: all 0.6s cubic-bezier(0.24, 0.75, 0.89, 0.74);
    height: 0;
    &__el{
        font-size: $fs-text;
        margin: 10px 30px;
        transition: all 0.2s ease;
    }
    &--actif{
        display: block;
        opacity: 1;
        visibility: visible;
        height: 100%;
    }
}
//houses
.housesList{
    margin: 60px 0 0 0;
    &__el{
        width: 80%;
        max-width: 280px;
        display: flex;
        flex-direction: column;
        margin-left: auto;
        margin-right: auto;
        transition: all 0.2s ease;
        cursor: pointer;
        & a{
        text-align: center; 
        }
        &:not(:last-child){
            margin-bottom: 45px;
        }
    }
}
//values
.valuesList{
    max-width: 612px;
    margin: 60px auto;
    &__el{
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 45px 0;
        font-size: $fs-text;
        & > span{
            width: 80px;
            height: 80px;
            position: relative;
            overflow: hidden;
            text-indent: -9999px;
            &:after, &:before{
                content: '';
                position: absolute;
            }
            &:after{
                display: block;
                width: 67px;
                height: 67px;
                top: 10px;
                left: 10px;
                background-color: $c-pink;
                border-radius: 50%;
            }
            &:before{
                width: 100%;
                height: 100%;
                background-repeat: no-repeat;
                background-position: center;
                top: 0;
                left: 0;
                z-index: 5;
            }
            &.welcomeValue{
                &:before{
                    width: 115%;
                    height: 115%;
                    background-image: url(../assets/images/welcome.svg);
                }
            }
            &.familyValue{
                &:before{
                    width: 100%;
                    height: 100%;
                    background-image: url(../assets/images/family.svg);
                }
            }
            &.wellnessValue{
                &:before{
                    background-image: url(../assets/images/wellness.svg);
                }
            }
            &.listeningValue{
                &:before{
                    background-image: url(../assets/images/ear.svg);
                }
            }
        }
        &--blue{
            & > span{
                &:after{
                    background-color: $c-blue;
                }
            }
        }
    }
}
//NEWSROOM
.newsOptions{
    width: fit-content;
    display: flex;
    background-color: $c-lightBlue;
    border-radius: 4px;
    margin: 60px auto;
    &__el{
        cursor: pointer;
        padding: 15px;
        font-size: $fs-indicators;
        transition: .4s ease-out;
        &--actif{
            background-color: $c-darkBlue;
            color: #fff;
            border-radius: 4px;
        }
    }
}
.newsList{
    margin: 0 0 60px 30px;
    display: flex;
    overflow-x: scroll;
    transition: .7s ease-out;
    &::-webkit-scrollbar {
        visibility: hidden;
    }
    &__el{
        width: 80%;
        max-width: 320px;
        flex-shrink: 0;
        &:not(:last-child){
            margin-right: 30px;
        }
        &:last-child{
            padding-right: 30px;
        }
        & .new{
            &__img{
                margin-bottom: 30px;
            }
            &__article{
                margin-top: 30px;
            }
        }
    }
}
.disappearArticle{
    opacity: 0;
}
//FOOTER
.footerList{
    width: 100%;
    text-align: center;
    font-size: $fs-text;
    & > :first-child{
        width: 160px;
        height: 37px;
        margin: 0 auto;
        overflow: hidden;
        background-image: url(../assets/images/logo.svg);
        background-repeat: no-repeat;
        text-indent: 100%;
        transition: .2s ease;
    }
    & > :last-child{
        font-size: $fs-indicators;
    }
    &__el{
        margin: 15px 0;
    }
}
.tabsList{
    width: 100%;
    text-align: center;
    & > :first-child{
        font-size: $fs-text;
        font-weight: 600;
        text-transform: uppercase;
        margin-bottom: 15px;
    }
    & > :not(:first-child){
        color: $c-lightGrey;
        margin-top: 15px;
        transition: .2s ease;
    }
    &:not(:last-child){
        margin: 60px 0;
    }
}
.socialMedia{
    width: 120px;
    display: flex;
    justify-content: space-between;
    margin: 15px auto 0 auto;
        &__el{
            width: 25px;
            height: 25px;
            overflow: hidden;
            background-repeat: no-repeat;
            background-size: 100%;
            text-indent: -999px;
            opacity: .55;
            transition: all 0.2s ease;
            cursor: pointer;
            &:first-child{
                width: 30px;
                height: 30px;
                background-image: url(../assets/images/twitter.svg);
            }
            &:nth-child(2){
                background-image: url(../assets/images/facebook.svg);
            }
            &:last-child{
                background-image: url(../assets/images/instagram.svg);
            }
        }
}
//media queries
@media(min-width: 650px){
    .headerList{
        &--small{
            width: 420px;
            position: relative;
        }
        &--main{
            margin: 80px 0 0 0;
            & .headerList__el{
                font-size: $fs-menuTab;
            }
        }
    }
    .housesList{
        max-width: 600px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin: 80px auto 0 auto;
        &__el{
            position: relative;
            &:not(:last-child){
                margin-bottom: 40px;
            }
            &:after{
                content: '';
                width: 240px;
                height: 240px;
                position: absolute;
                background-color: $c-lightBlue;
                border-radius: 50%;
                z-index: -2;
                transition: all 0.2s ease;
            }
            &:first-child, &:nth-child(3){
                margin: 0 40px 0 0;
                &:after{
                    top: -60px;
                    left: -60px;
                }
            }
            &:nth-child(2), &:last-child{
                margin-left: 0;
                margin-right: 0;
                &:after{
                    top: 60px;
                    right: -60px;
                }
            }
        }
    }
    .valuesList{
        margin: 80px auto;
        &__el{
            flex-direction: row;
            align-items: center;
            margin: 40px 0;
            & span{
                margin-right: 40px;
            }
        }
    }
    .newsOptions{
        margin: 80px auto;
    }
    .tabsList{
        width: 240px;
        text-align: left;
        margin-top: 40px !important;
        &:last-child{
            padding: 0;
        }
        &:first-child{
            margin-top: 80px;
        }
        &:not(:last-child){
            margin: 0;
        }
    }
    .socialMedia--footer{
        width: inherit;
        justify-content: flex-start;
        & .socialMedia__el{
            width: 20px;
            height: 20px;
            &:not(:last-child){
                margin-right: 20px;
            }
            &:first-child{
                width: 25px;
                height: 25px;
            }
        }
    }
    .newsList{
        overflow: visible;
        &__el{
            & .new{
                transition: .2s ease;
                &__img{
                    margin-bottom: 40px;
                }
                &__article{
                    margin-top: 40px;
                }
                &:hover{
                    transform: scale(1.05);
                }
            }
        }
    }
}
//hover effects
@media(min-width: 950px){
    .headerList{
        &--small{
            & .headerList__el{
                &:not(:last-child):hover{
                    opacity: .5;
                }
                & .socialMedia{
                    &__el{
                        &:hover{
                            opacity: .5;
                        }
                    }
                }
            }
        }
        &--main{
            & .headerList__el:not(:first-child), & .headerList__el .dropdownLink{
                &:hover{
                    color: $c-indicators;
                }
            }
            & .headerList__el .dropdownList{
                &__el:hover{
                    background-color: $c-pink;
                    color: $c-paragraph;
                }
            }
        }
    }
    .housesList{
        &__el{
            &:hover{
                transform: scale(1.05);
                &:after{
                    width: 240px;
                    height: 240px;
                }
            }
        }
    }
    .newsOptions{
        &__el{
            &:hover{
                background-color: $c-blue;
                border-radius: 4px;
            }
            &--actif:hover{
                background-color: $c-darkBlue;
            }
        }
    }
    .footerList{
        & li{
            cursor: pointer;
        }
        & > :first-child:hover{
            transform: scale(1.05);
        }
        & > :nth-child(2):hover, & > :last-child:hover{
            color: $c-title;
            font-weight: 500;
        }
    }
    .tabsList{
        & > :not(:first-child){
            &:hover{
                color: $c-paragraph;
                font-weight: 500;
            }
        }
    }
    .socialMedia--footer{
        & .socialMedia__el:hover{
            opacity: .8;
        }
    }
}
@media(min-width: 1150px){
    .headerList{
        width: 100%;
        justify-content: flex-end;
        &__el{
            position: relative;
            &:not(:last-child){
                margin: 0 40px 0 0;
            }
        }
        &--small{
            margin: 0;
            bottom: 0;
            & .headerList__el{
                font-size: $fs-indicators;
            }
            & > :last-child{
                margin: 0;
            }
        }
        &--main{
            margin: 0;
            flex-direction: row;
            & .headerList__el{
                font-size: $fs-text;
                &:not(:last-child){
                    margin-bottom: 0;
                }
            }
            & > :first-child{
                margin-right: 60px;
            }
        }
    }
    .dropdownList{
        display: block;
        position: absolute;
        height: auto;
        width: 280px;
        top: 48px;
        background-color: #fff;
        box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
        transition: ease .4s;
        &__el{
            padding: 10px 20px;
            margin: 0;
            font-size: $fs-indicators;
        }
    }
    .socialMedia{
        &__el{
            width: 15px;
            height: 15px;
            &:first-child{
                width: 20px;
                height: 20px;
            }
        }
    }
    .housesList{
        margin: 0;
        justify-content: flex-end;
        &__el{
            max-width: 220px;
            position: relative;
            &:after{
                content: '';
                width: 220px;
                height: 220px;
                position: absolute;
                background-color: $c-lightBlue;
                border-radius: 50%;
                z-index: -2;
            }
            &:first-child, &:nth-child(3){
                &:after{
                    top: -40px;
                    left: -40px;
                }
            }
            &:nth-child(2), &:last-child{
                &:after{
                    top: 40px;
                    right: -40px;
                }
            }
        }
    }
    .valuesList{
        margin: 120px auto;
    }
    .newsOptions{
        margin: 80px auto;
        font-size: $fs-text;
    }
    .newsList{
        width: 100%;
        justify-content: center;
        &__el{
            &:last-child{
                padding-right: 0;
            }
        }
    }
    .footerList{
        padding: 0;
    }
    .tabsList{
        width: fit-content;
        max-width: 200px;
        font-size: 13px;
        margin-top: 0 !important;
        margin-bottom: 0 !important;
        &:not(:last-child){
            margin-right: 40px !important;
        }
        & > :first-child{
            font-size: 13px;
        }
        & li{
            &:not(:last-child){
                margin-bottom: 10px;
            }
        }
        &:last-child{
            padding: 0;
        }
    }
}
@media(min-width: 1400px){
    .valuesList{
        max-width: inherit;
        position: relative;
        &:after{
            content: '';
            width: 500px;
            height: 100%;
            position: absolute;
            top: 0;
            right: -145px;
            background-image: url(../assets/images/values.jpg);
            background-repeat: no-repeat;
            background-position: 100%;
            border-radius: 7px 0 0 7px;
        }
    }
}