//TITLES
.title{
    font-family: "Beau", Helvetica, Arial, sans-serif;
    font-size: $fs-titleMob;
    font-weight: 500;
    color: $c-title;
    margin-bottom: 30px;
    max-width: 480px;
    line-height: 1.25;
    transition: all 0.2s ease;
    &:not(:first-child){
        margin: 30px 0;
    }
    &--houses{
        &:before{
            content: 'nos maisons';
            display: block;
            margin-bottom: 30px;
            text-transform: uppercase;
            font-family: zeitung, sans-serif;
            font-size: $fs-indicators;
            font-weight: 600;
            color: $c-indicators;
        }
    }
    &--values{
        margin-right: auto;
        margin-left: auto;
        &:before{
            content: 'complément titre valeurs';
            display: block;
            margin-bottom: 30px;
            text-transform: uppercase;
            font-family: zeitung, sans-serif;
            font-size: $fs-indicators;
            font-weight: 600;
            color: $c-indicators;
        }
    }
    &--newsroom{
        margin-right: auto;
        margin-left: auto;
        &:before{
            content: 'la vie chez seniorissim';
            display: block;
            margin-bottom: 30px;
            text-transform: uppercase;
            font-family: zeitung, sans-serif;
            font-size: $fs-indicators;
            font-weight: 600;
            color: $c-indicators;
        }
    }
    &--subtitle{
        margin: 30px 0 15px 0 !important;
        font-family: zeitung, sans-serif;
        font-size: $fs-subtitle;
        font-weight: 600;
        color: $c-subtitle;
        &  + small{
            font-size: $fs-bigText;
            color: $c-lightGrey;
        }
    }
    &--value{
        text-align: center;
    }
    &--news{
        margin: 0 !important;
    }
}
//media queries
@media(min-width: 650px){
    .title{
        max-width: 520px;
        font-size: $fs-titleTab;
        margin-bottom: 40px;
        &:not(:first-child){
            margin: 40px 0;
        }
        &--houses{
            &:before{
                margin-bottom: 40px;
            }
        }
        &--values{
            &:before{
                margin-bottom: 40px;
            }
        }
        &--news{
            &:before{
                margin-bottom: 40px;
            }
        }
        &--subtitle{
            font-size: $fs-subtitle;
        }
        &--value{
            text-align: left;
            margin-top: 0 !important;
        }
        &--news{
            margin: 0 !important;
        }
    }
}
@media(min-width: 1150px){
    .title{
        &--subtitle{
            font-size: $fs-bigText;
            margin-bottom: 20px !important;
        }
        &--news{
            margin: 0 !important;
        }
    }
}