//IMAGES
.img{
    border-radius: 7px;
    transition: all 0.2s ease;
    &--intro{
        width: 420px;
        display: block;
        margin: 40px auto 0 auto;
        position: relative;
        border-radius: 7px;
    }
    &--houses{
        width: 100%;
        overflow: hidden;
    }
}
//media queries
@media(min-width: 1150px){
    .img{
        &--intro{
            width: 440px;
            margin: 0;
        }
    }
}