body {
  width: 100vw;
  overflow-x: hidden;
  font-family: zeitung, sans-serif;
  font-weight: 300;
  font-style: normal;
  line-height: 1.75;
  font-size: $fs-bigText;
  color: $c-paragraph;
  margin: 0 auto;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-tap-highlight-color: transparent;
}
.menuOpen{
  overflow: hidden;
}
*{
  margin: 0;
}
p{
  max-width: 480px;
}

@font-face {
	font-family: "Beau";
	src: url('../assets/font/beau.ttf');
}
@media(min-width: 500px){
  .blockMode{
    display: block;
  }
}
