//SECTIONS
.section{
    padding: 90px 30px;
    max-width: 1400px;
    box-sizing: border-box;
    margin: 0 auto;
    &--intro{
        height: calc(100vh - 90px);
        margin-top: 100px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0 30px;
        & .intro__img{
            display: none;
            position: relative;
            &:after{
                content: '';
                width: 360px;
                height: 360px;
                position: absolute;
                background-color: $c-lightBlue;
                border-radius: 50%;
                top: 0;
                right: 0;
                z-index: -2;
            }
        }
    }
    &--values{
        width: 100vw;
        max-width: 100vw;
        box-sizing: border-box;
        clip-path: ellipse(155% 50% at 50% 50%);
        background-color: $c-lightBlue;
        & .valuesText{
            width: 100%;
            max-width: 480px;
            margin: 0 auto;
            & > :not(:last-child){
                text-align: center;
            }
        }
    }
    &--news{
        padding: 90px 0;
        overflow-x: hidden;
        & > :first-child, & > :nth-child(2){
            width: 100vw;
            padding: 0 30px;
            box-sizing: border-box;
        }
        & > :first-child{
            text-align: center;
        }
        & > :nth-child(2){
            margin: 0 auto;
        }
    }
}
//media queries
@media(min-width: 650px){
    .section{
        padding: 120px 60px;
        &--intro{
            padding: 0 60px;
            position: relative;
            & .intro__img{
                display: block;
            }
        }
    }
}
@media(min-width: 750px){
    .section{
        &--intro{
            & .intro__img{
                &:after{
                    right: 20px;
                }
            }
        }
        &--values{
            clip-path: ellipse(110% 50% at 50% 50%);
        }
    }
}
@media(min-width: 850px){
    .section{
        &--intro{
            & .intro__img{
                &:after{
                    right: 80px;
                }
            }
        }
    }
}
@media(min-width: 950px){
    .section{
        &--intro{
            & .intro__img{
                &:after{
                    right: 120px;
                }
            }
        }
    }
}
@media(min-width: 1150px){
    .section{
        padding: 160px 80px;
        &--intro{
            padding: 0 80px;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            & .intro__img{
                &:after{
                    width: 440px;
                    height: 440px;
                    right: -80px;
                    top: -80px;
                }
            }
        }
        &--houses{
            display: flex;
            & > :first-child{
                width: 55%;
            }
        }
        &--values{
            clip-path: ellipse(90% 50% at 50% 50%);
        }
    }
}
@media(min-width: 1250px){
    .section{
        padding: 160px 145px;
        &--intro{
            padding: 0 145px;
            & .intro__img{
                &:after{
                    right: -100px;
                }
            }
        }
    }
}
@media(min-width: 1400px){
    .section{
        &--values{
            left: 0;
        }
    }
}